// src/components/shell/AppShell.tsx
"use client";

import { useEffect, useState } from "react";
import Sidebar from "@/components/Sidebar";
import HeaderMinimal from "@/components/HeaderMinimal";
import clsx from "clsx";

type Props = {
  children: React.ReactNode;
  isAdmin?: boolean;
};

const LS_KEY = "ui.sidebar.collapsed";

export default function AppShell({ children, isAdmin }: Props) {
  const [collapsed, setCollapsed] = useState<boolean>(false);

  // carrega preferencia
  useEffect(() => {
    try {
      const v = localStorage.getItem(LS_KEY);
      if (v === "1") setCollapsed(true);
    } catch {}
  }, []);

  // ouve o botão do header
  useEffect(() => {
    function onToggle(e: Event) {
      const detail = (e as CustomEvent<string>).detail;
      setCollapsed((cur) => {
        let next = cur;
        if (detail === "toggle") next = !cur;
        if (detail === "open") next = false;
        if (detail === "close") next = true;
        try {
          localStorage.setItem(LS_KEY, next ? "1" : "0");
        } catch {}
        return next;
      });
    }
    window.addEventListener("estudorank:sidebar" as any, onToggle);
    return () =>
      window.removeEventListener("estudorank:sidebar" as any, onToggle);
  }, []);

  // largura do rail
  const railW = collapsed ? "w-[72px]" : "w-[248px]";

  return (
    <div className="min-h-screen bg-[var(--app-bg)] text-white">
      <HeaderMinimal />
      <div className="max-w-7xl mx-auto px-3 md:px-6 pt-4 pb-10">
        <div className="flex gap-4">
          {/* Sidebar */}
          <aside
            className={clsx(
              "transition-[width] duration-300 ease-out flex-shrink-0",
              railW
            )}
          >
            <Sidebar collapsed={collapsed} isAdmin={isAdmin} />
          </aside>

          {/* Conteúdo */}
          <main className="flex-1 min-w-0">{children}</main>
        </div>
      </div>
    </div>
  );
}
